import { apiBaseUrl } from '@/common/setting/constVarsHelper.js';
import * as db from '@/common/utils/dbHelper.js' //common


// VueVue()vmmain.jsVue.use(httpInterceptor, app)
// Vue"this"
// installhttps://uviewui.com/components/vueUse.html
const install = (Vue, vm) => {
    // 
    Vue.prototype.$u.http.setConfig({
        baseUrl: apiBaseUrl, // 
        method: 'POST',
        dataType: 'json',// jsonJSON.parse()
        showLoading: true, // loading
        loadingText: '...', // loading
        loadingTime: 800, // ms
        originalData: false, // 
        loadingMask: true, // loading
        // 
        header: {
            'Content-type': 'application/json'
        },
    });

    // 
    Vue.prototype.$u.http.interceptor.request = (config) => {
        if (config.header.needToken) {
            // token
            const userToken = db.get("userToken");
            if (!userToken) {
                console.log("");
                Vue.prototype.$store.commit('showLoginTip', true);
                return false;
            } else {
                config.header.Authorization = 'Bearer ' + userToken;
            }
        }
        //
        if (config.header.method == 'user.share') {
            const userToken = db.get("userToken");
            config.header.Authorization = 'Bearer ' + userToken;
        }
        return config;
    }
    // 
    Vue.prototype.$u.http.interceptor.response = (result) => {
        let pages = getCurrentPages();
        var page = pages[pages.length - 1];

        if (!result.status && page) {
            //console.log(page.route);
            // 
            if (result.data === 14007 || result.data === 14006) {
                db.del("userToken");
                console.log("");
                //Vue.prototype.$store.commit('showLoginTip', true);
                Vue.prototype.$store.commit('hasLogin', false);
            }
        }

        return result;
        // rescoderesult
        // res.resultthis.$u.post(url).then(res => {})thenres
        // originalDatatrue
    }
}

export default {
    install
}