export const tools = {
    methods: {
        doCopyData(data) {
            var _this = this;
            uni.setClipboardData({
                data: data,
                success: function () {
                    _this.$u.toast('')
                }
            });
        },
        doPhoneCall() {
            var phome = this.$store.state.config.shopMobile || 0;
            if (phome != 0) {
                uni.makePhoneCall({
                    phoneNumber: phome
                });
            }
        },
        goBack() {
            //
            const pages = getCurrentPages();
            if (pages.length === 2) {
                uni.navigateBack({
                    delta: 1
                });
            } else if (pages.length === 1) {
                uni.switchTab({
                    url: '/pages/index/default/default',
                })
            } else {
                uni.navigateBack({
                    delta: 1
                });
            }
        },
    }
}
